.loaded nav.main-nav {
  display: block; }

nav.main-nav.dark, .mobile nav.main-nav {
  color: #424242;
  background: #f5f5f5;
  -webkit-box-shadow: 0 5px 10px rgba(117, 117, 117, 0.1);
          box-shadow: 0 5px 10px rgba(117, 117, 117, 0.1); }
  nav.main-nav.dark a, nav.main-nav.dark strong, .mobile nav.main-nav a, .mobile nav.main-nav strong {
    color: #424242; }
  nav.main-nav.dark li:not(:last-child), .mobile nav.main-nav li:not(:last-child) {
    border-right: 2px solid #424242; }

nav.main-nav {
  display: none;
  position: fixed;
  width: 100%;
  z-index: 3;
  color: #ffffff;
  -webkit-transition: background .3s, -webkit-box-shadow .3s;
  transition: background .3s, -webkit-box-shadow .3s;
  -o-transition: background .3s, box-shadow .3s;
  transition: background .3s, box-shadow .3s;
  transition: background .3s, box-shadow .3s, -webkit-box-shadow .3s; }
  nav.main-nav strong {
    color: #ffffff;
    font-size: 20px;
    -webkit-transition: color .3s;
    -o-transition: color .3s;
    transition: color .3s; }
  nav.main-nav a {
    color: #ffffff;
    font-weight: 400;
    text-decoration: none;
    -webkit-transition: color .3s;
    -o-transition: color .3s;
    transition: color .3s; }
  nav.main-nav ul {
    list-style-type: none; }
    nav.main-nav ul li {
      display: inline-block;
      margin-right: 10px;
      padding-right: 10px; }
      nav.main-nav ul li:not(:last-child) {
        border-right: 2px solid #ffffff;
        -webkit-transition: border .3s;
        -o-transition: border .3s;
        transition: border .3s; }



